import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import TicketHead from '../components/TicketHead';
import './packageDetail.scss';
import header from '../images/header.jpg';
import req from '../io/req';
const log = console.log;

function Detail(props) {
  const [data, setData] = useState({});
  const params = props.match.params;

  useEffect(() => {
    req('get')('/combo?id='+params.id).then(setData);
  }, []);


  return(
    <div className='p-package-detail'>
      <img src={header} alt="标题图片" className="header"/>
      <div className="content">
        <TicketHead {...data} />
        <div className="imgs">
          {data.images ? data.images.map(
            img => <img className='image' src={img} key={Math.random()} alt='' />
          ) : ''}
        </div>
      </div>
      <div className="btn">
        <Link to={'/signup/'+params.id}>
          <div className="btn-content">我要报名</div>
        </Link>
      </div>
    </div>
  );
}

export default Detail;
