import React, { useState, useEffect } from 'react';
import { List, InputItem, Picker, DatePicker } from 'antd-mobile';
import { Link } from 'react-router-dom';
import province from '../province.json';
import './playerInput.scss';
import req from '../io/req';
import { isArray } from 'util';
const log = console.log;
const token = 'E/jXx3GWQXu6tiIeIT32iw==';

const single = [
  {
    label: 'a',
    value: 'A'
  }, {
    label: 'b',
    value: 'B'
  }
];

/**
 * @param {*} props
 * 这个组件真的是屎一样的组件！
 * type=5 时的处理真令人窒息！
 */
function Item(props) {
  // 0: 空行
  // 1: 普通填写
  // 2: 单选
  // 3: 日期
  // 4: 地址
  // 5: 证件
  const {
    type=0,
    label,
    placeholder,
    callback
  } = props;

  const [height, setHeight] = useState(0);
  const [state, setState] = useState('normal');
  const [value, setValue] = useState(props.value || '');
  const [firstTime, setFirstTime] = useState(true);
  const [data5, setData5] = useState(props.value);

  let extraContent = null;
  let checkResult = null;
  let inputItem = null;
  let warn = null;
  let tip = null;
  let tipH = 0;
  let rule = props.rule;
  let ok = false;
  if (typeof value === 'string') {
    ok = Boolean(value);
  }
  if (isArray(value)) {
    ok = value.length > 0;
  }
  if (typeof value === 'object') {
    ok = true;
  }

  if (props.tips) {
    const tips = props.tips.trim();
    const tipArr = tips.split('\n');
    if (!tipArr[tipArr.length - 1]) {
      tipArr.pop();
    }
    tipH = 8 + 18 * tipArr.length;
    tip = tipArr.map(tip => <p key={tip+Math.random()}>{tip}</p>);
  }

  if (props.warn) {
    warn = <div
      className='error'
      style={{height: height}}
    >{props.warn}</div>;
  }

  if (tip || warn) {
    extraContent = <div>
      {warn}
      {tip}
    </div>;
  }

  function checkRule(deepProp) {
    if (deepProp && typeof deepProp !== 'string') {
      return callback && callback({
        ...deepProp,
        value
      });
    }
    if (!rule) {
      return callback && callback({
        ...props,
        value,
        checkResult
      });
    }
    const reg = new RegExp(rule);
    const result = reg.test(value);
    if (result) {
      setState('normal');
      setHeight(0);
      // check pass
      checkResult = false;
    } else {
      setState('error');
      setHeight(18);
      checkResult = checkRule;
    }
    callback && callback({
      ...props,
      value,
      checkResult
    });
  }

  function handleResult(val) {
    setValue(val);
    setState('normal')
    callback && callback({
      ...props,
      checkResult: false,
      value: val
    });
  }

  useEffect(() => {
    callback && callback({
      ...props,
      checkResult: checkRule
    });
    if (!firstTime) {
      checkRule();
    } else {
      setFirstTime(false);
    }

  }, [props.rule]);

  if (type === 0) {
    // 空行
    inputItem = <div className='white-block'/>;
  }

  if (type === 1) {
    // 普通填写
    inputItem = <InputItem
      clear
      labelNumber={7}
      value={value}
      onChange={setValue}
      onBlur={checkRule}
      onFocus={() => {
        setState('normal');
        setHeight(0);
      }}
      placeholder={placeholder}
    >{label}</InputItem>;
  }

  if (type === 2) {
    // 单选菜单
    inputItem = <Picker
      data={props.items}
      cols={1}
      value={value}
      onOk={handleResult}
      extra={placeholder}
    >
      <List.Item arrow="horizontal">{label}</List.Item>
    </Picker>;
  }

  if (type === 3) {
    // 日期选择
    inputItem = <DatePicker
      mode='date'
      minDate={new Date('1980-01-01')}
      extra={placeholder}
      value={value}
      onChange={handleResult}
    >
      <List.Item arrow="horizontal">{label}</List.Item>
    </DatePicker>;
  }

  if (type === 4) {
    // 地址选择
    inputItem = <Picker
      data={province}
      cols={3}
      value={value}
      onOk={handleResult}
      extra='请选择地区'
    >
      <List.Item arrow="horizontal">地区</List.Item>
    </Picker>;
  }

  if (type === 5) {
    // 该死的复合组件，证件选择
    function setValue5(i) {
      return function(pro) {
        const arr = data5.concat();
        arr[i] = pro;
        setData5(arr);
        if (data5[1]) {
          // if (!isArray(arr[0])) {
          //   // arr[0] = [arr[0],]
          // }
          callback({
            ...props,
            // value: [findSfz(arr[0].value[0]), arr[1].value],
            value: [arr[0], arr[1].value],
            checkResult: data5[1].checkResult
          }, false);
        }
      }
    }

    const Up = <Item
      type={2}
      label='证件类型'
      placeholder='请选择类型'
      value={findReg(value[0])}
      items={props.items}
      callback={setValue5(0)}
    />;

    const Down = <Item
      type={1}
      label='证件号'
      placeholder='请输入证件号'
      value={data5[1] || value[1] || ''}
      rule={data5[0].value ? data5[0].value[0] : ''}
      callback={setValue5(1)}
    />;
    inputItem = <>{Up}{Down}</>;
  }

  function findReg(sfz) {
    if (!sfz) {
      return;
    }
    const list = props.items;
    return [list.filter(obj => obj.label === sfz)[0].value];
  }
  function findSfz(reg) {
    const list = props.items;
    return list.filter(obj => obj.value === reg)[0].label;
  }

  const classes = `input-box ${state === 'error' ? 'input-error' : ''} ${state === 'warn' ? 'input-warn' : ''}${ok ? ' okk' : ''}`;
  const extra = extraContent ? <div className='input-extra'>{extraContent}</div> : null;
  return (
    <div className={classes}>
      {inputItem}
      {extra}
    </div>
  );
}

// ----------------------------------------------------------------------------

function Entry(props) {
  const id = props.match.params.id;
  const [list, setList] = useState([]);
  const [items, setItems] = useState([]);

  function cb(obj) {
    const newItems = items.concat();
    newItems[obj.index] = obj;
    setItems(newItems);
  }

  function handleH() {
    log(items)
  }

  const itemList = list.map((el, index) => {
    if (el.value && el.type === 5 && typeof el.value === 'string') {
      el.value = el.value.split(',');
    }
    if (el.value && el.type === 2) {
      el.value = [el.value];
    }
    if (el.value && el.type === 3) {
      el.value = new Date(Number(el.value));
    }
    if (el.value && el.type === 4 && !isArray(el.value)) {
      el.value = el.value.split(',');
    }
    return(
      <Item
        {...el}
        key={index}
        keyName={el.key}
        index={index}
        callback={cb}
      />
    );
  });

  useEffect(() => {
    let url = '/user/formInfo?comboId=xxx';
    req('get')('/user/formInfo?comboId='+id).then(res => {
      setList(res || []);
    })
  }, []);

  function handleSubmit() {
    log(props)
    const pushData = {};
    items.forEach(item => {
      if (!item.keyName) {
        return;
      }
      let value = item.value;
      if (isArray(item.value)) {
        value = item.value.join(',');
      }
      if (item.value.getDate) {
        value = Number(item.value) / 1000;
      }
      pushData[item.keyName] = value;
    });
    req('post')('/user?token='+token, pushData)
      .then(res => {
        log(res)
        if (res.userId) {
          const push = props.history.push;
          push('/signup/'+id, {
            open: true
          })
        }
      })
  }

  return(
    <div className='p-player-input'>
      <div className="yellow-title"
        onClick={handleH}
      >新增选手信息</div>

      <List>
        {itemList}
      </List>
      <div className="bottom-box">
        {/* <Link to='/signup'> */}
          <div
            className="btn"
            onClick={handleSubmit}
          >完成</div>
        {/* </Link> */}
      </div>
    </div>
  );
}

export default Entry;
