import React from 'react';
import bird from '../images/bird-detail.png';

function Head(props) {
  // console.log(props)
  let price = null;
  if (props.price !== undefined) {
    price = <p className="price-line">
      { props.isBird && <img src={bird} alt="早鸟价" className="bird"/> }
      <span className="red-price">{props.isBird ? Number(props.discountPrice).toFixed(2) : Number(props.price).toFixed(2)}</span>
      { props.oldPrice && <span className="old-price">￥{props.price.toFixed(2)}</span> }
    </p>
  }
  return(
    <div className='c-ticket-head'>
      {price}
      <p className="ticket-title">{props.name || '没有标题'}</p>
      <p className="ticket-info">比赛日期：<span>{new Date(props.actualTime).Format('yyyy-MM-dd')}</span></p>
      <p className="ticket-info">比赛地点：<span>{props.address}</span></p>
      <p className="ticket-info">截止报名：<span>{new Date(props.endTime).Format('yyyy-MM-dd')}</span></p>
    </div>
  );
}

export default Head;
