import React, { useState, useEffect } from 'react';
import { Toast, Modal } from 'antd-mobile';
import TicketHead from '../components/TicketHead';
import Players from '../components/Players';
import { Link } from 'react-router-dom';
import req from '../io/req';
import './signUp.scss';
const log = console.log;
const token = 'E/jXx3GWQXu6tiIeIT32iw==';

function SignUp(props) {
  const id = props.match.params.id;
  const [data, setData] = useState({});
  const [ticket, setTicket] = useState({});
  const [players, setPlayers] = useState({});
  const [playerList, setPlayerList] = useState([]);
  const [price, setPrice] = useState(0);
  let open = false;

  const queryState = props.location.state;
  if (queryState) {
    open = queryState.open;
  }

  useEffect(() => {
    setTicket(props.location.query || {});

    req('get')('/combo?id='+id).then(setData);
    const config = {
      token,
      comboId: id
    };
    req('get')('/user/exists', config)
      .then(setPlayers);
  }, []);

  return(
    <div className='p-sign-up'>
      <div className="header">
        <div className="bg-layout">
          <img className='bg-img' src={data.picture} alt=""/>
        </div>
        <div className="bg">
          <TicketHead {...data} />
        </div>
      </div>
      <Players
        {...data}
        {...players}
        editable
        open={open}
        cb={setPlayerList}
      />
      <div className="br"/>
      <Payment
        price={price}
        ticket={ticket}
        players={playerList}
        id={id}
        push={props.history.push}
      />
    </div>
  );
}

function Payment(props) {
  const { players } = props;
  const [promoCode, setPromoCode] = useState('');
  const [price, setPrice] = useState(props.price);
  const prompt = Modal.prompt;

  useEffect(() => {
    setPrice(props.price);
  }, [props.price]);

  function checkPromo(promo) {
    if (promo.toLowerCase() === 'qep') {
      setPromoCode(promo);
      setPrice(price - 10);
      return Toast.info('兑换成功', 1.2);
    }
    return Toast.info('优惠码错误', 1.2);
  }

  function inputPromo() {
    prompt('请输入优惠码', null, [
      { text: '取消' },
      {
        text: '确定',
        onPress: code => checkPromo(code)
      }
    ]);
  }

  function handlePay() {
    log(players);
    const data = {
      comboId: props.id,
      account: props.price,
      saleCode: promoCode,
      willUserId: players.map(player => player.id).join(',')
    };
    log(data)
    req('post')('/user/order?token='+token, data)
      .then(res => {
        props.push('/success/'+res)
      });
  }

  return(
    <div className='payment'>
      <div className="line-item">
        <div className="label">优惠码</div>
        { promoCode
          ? <div className='label-content'>{ promoCode }</div>
          : <>
            <div
              className="preferential"
              onClick={inputPromo}
            >请输入优惠码</div>
            <div className="arrow"></div>
          </>
        }
      </div>
      <div className="line-item">
        <div className="label">支付方式</div>
        <div className="label-content">微信支付</div>
      </div>
      <div className="pay-bottom">
        <p className='pay-price'>
          <span className="pay-label">总价</span>
          <span className="red-price">{price.toFixed(2)}</span>
        </p>
        <div className="pay-btn" onClick={handlePay}>
          {/* <Link to={{
            pathname: '/success',
            query: {
              ticket: props.ticket,
              players: props.players,
              price
            }
          }}> */}
            去支付
            {/* </Link> */}
        </div>
      </div>
    </div>
  );
}

export default SignUp;
